import { useMemo } from "react";
import { connect } from "react-redux";

import {
  R_F_change_favorite,
  R_F_change_limit,
  R_F_change_page,
} from "../../redux/gallery/reduce_Gallery";
import PaginationLimit from "./PaginationLimit";
import PaginationPage from "./PaginationPage";
import PhotoItem from "./PhotoItem";

const mapStateToProps = (state) => {
  return {
    reducer_Gallery: state.reducer_Gallery,
  };
};

const Gallery = ({
  reducer_Gallery,
  R_F_change_favorite,
  R_F_change_limit,
  R_F_change_page,
}) => {

  const look_photo = useMemo(() => {
    return reducer_Gallery.allPhoto.map((i) => {
      const isBool = reducer_Gallery.favorite.length
        ? reducer_Gallery.favorite.find((el) => el.id === i.id)
          ? true
          : false
        : false;

      return (
        <PhotoItem
          key={i.id}
          item={i}
          R_F_change_favorite={R_F_change_favorite}
          favorite={isBool}
        />
      );
    });
  }, [reducer_Gallery]);

  return (
    <div className="gallery">
      <PaginationLimit
        page={reducer_Gallery.page}
        limit={reducer_Gallery.limit}
        R_F_change_limit={R_F_change_limit}
      />

      <div className="gallery_item">
        {look_photo.length ? (
          look_photo
        ) : (
          <img
            alt="error"
            src="data:image/png;base64,"
          />
        )}
      </div>
      <PaginationPage
        limit={reducer_Gallery.limit}
        max={look_photo.length}
        page={reducer_Gallery.page}
        R_F_change_page={R_F_change_page}
      />
    </div>
  );
};

export default connect(mapStateToProps, {
  R_F_change_favorite,
  R_F_change_page,
  R_F_change_limit,
})(Gallery);
